.nav {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 1rem;
  width: 100%;

  // font-md
  font-size: 20px;
  letter-spacing: 0.015em;
  line-height: 1.3;
}

.nav__right {
  display: flex;
  flex-grow: 1;
  flex-wrap: wrap;
  justify-content: flex-end;

  .nav__link-span {
    margin-left: 0.25em;
  }
}

.nav__link-span {
  background-color: var(--white);
  display: inline-block;
  padding: 0.25em;

  &.active {
    -webkit-font-smoothing: antialiased;
    background-color: var(--black);

    .nav__link {
      border-bottom: none;
      color: var(--white) !important;
    }
  }
}

.nav__link {
  color: var(--black) !important;
}

@media screen and (max-width: 950px) {
  .nav__left {
    width: 100%;
  }

  .nav__right {
    margin-top: 0.5em;
    justify-content: space-between;
    width: 100%;

    .nav__link-span {
      margin-left: 0;
      width: 50%;
    }
  }
}
